@import url(https://fonts.googleapis.com/css?family=Do+Hyeon);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);
body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	background: url(/Emoji-Dictionary/static/media/image.2c418ceb.jpg) no-repeat center; 
	background-size: cover;
}


//background: linear-gradient(to left, #ffdebe 0%, #ffefdf 100%);
.rowImg {
	border-radius: 41%;
	width: 90px;
	height: 90px;
}

h2 {
	text-align: left;
	font-family: 'Do Hyeon', sans-serif;
	padding-top: 5px;
	font-size: 1.7rem;
	padding-left: 20px;
}

.box{
	font-family: 'Do Hyeon', sans-serif;
	font-size: 25px;
	background-color: #cccccc;
	box-shadow: 10px 13px 9px -3px rgba(0,0,0,0.6);
}

::-webkit-input-placeholder {
	color: #333300;
	font-weight: 100;
	font-family: 'FontAwesome', 'Do Hyeon', sans-serif;
}

:-ms-input-placeholder {
	color: #333300;
	font-weight: 100;
	font-family: 'FontAwesome', 'Do Hyeon', sans-serif;
}

::placeholder {
	color: #333300;
	font-weight: 100;
	font-family: 'FontAwesome', 'Do Hyeon', sans-serif;
}


